<template>
  <div style="margin-top:15px;">
    <div>
      <div style="text-align: left;padding:0 0 10px 20px;font-size:0.8rem;">完善身份信息</div>
      <mt-field label="姓名"
                v-model="userName"
                placeholder="请输入完整姓名"></mt-field>
      <mt-field label="证件类型"
                v-model="type"
                readonly></mt-field>
      <mt-field label="证件号"
                v-model="IDcard"
                :attr='{ maxlength: 18 }'
                type='text'
                placeholder="请输入证件号"></mt-field>
      <div class="long"
           @click="btn_choose">
        <div>房间号</div>&emsp;&emsp;
        <div>{{ long_num }}&emsp;></div>
      </div>
      <div style="margin-top:20px;">
        <mt-field label="手机号"
                  v-model="phone"
                  placeholder="请输入手机号"
                  :attr='{ maxlength: 11 }'
                  type="tel"></mt-field>
        <mt-field label="车牌号"
                  v-model="carNum"
                  placeholder="请输入车牌号"></mt-field>
      </div>
      <mt-popup v-model="show_num"
                v-if="numList.length != 0"
                position="bottom"
                class="popup">
        <div v-for="(item,i) in numList"
             :key="i"
             class="num_box"
             @click="choosed(item)">{{ item.roomSn }}</div>
      </mt-popup>
      <mt-button type="primary"
                 @click="bthDeal"
                 style="margin-top:50px;width:90%;background-color:#f60;">办理入住</mt-button>
    </div>
  </div>
</template>
<script>
import { GetBusinessOrderroomByorder, GetBusinessRoomFreeRoomscale, PostBusinessOrderRoomin, PostBusinessOrderroomRoomin } from '@/api/api_business_hotel_and_scale';
import { Toast, MessageBox } from 'mint-ui';
export default {
    data() {
        return {
            userName: '',
            type: '身份证',
            IDcard: '',
            phone: '',
            carNum: '',
            month: '',
            long_num: '请选择房间号',
            numList: [],
            show_num: false
        };
    },
    created() {
        this.$store.commit('setHeader', '办理入住');
        if (JSON.parse(localStorage.getItem('orderday'))) {
            this.day = JSON.parse(localStorage.getItem('orderday'));
            this.userName = this.day.roomUser;
            this.orderId = this.day.id;
        } else if (JSON.parse(localStorage.getItem('ordermonth'))) {
            this.month = JSON.parse(localStorage.getItem('ordermonth'));
            this.userName = this.month.roomUser;
            this.orderId = this.month.id;
        }
        this.getinfo_id();
        this.getNum();
    },
    methods: {
        // 查询id
        getinfo_id() {
            GetBusinessOrderroomByorder(this.orderId).done((res) => {
                this.crrentId = res.data[0].id;
            });
        },
        // 房间号
        getNum() {
            let virtualRoomScaleId = this.month.virtualRoomScaleId ? this.month.virtualRoomScaleId : this.day.virtualRoomScaleId;
            GetBusinessRoomFreeRoomscale(virtualRoomScaleId).done((res) => {
                this.numList = res.data;
                this.long_num = this.numList[0].roomSn;
                this.roomId = this.numList[0].id;
                if (this.numList.length == 0) {
                    Toast({
                        message: '暂无空闲房间可选择',
                        duration: 900
                    });
                }
            });
        },
        // 选择房间号-弹出
        btn_choose() {
            this.show_num = true;
        },
        // 选择房间号
        choosed(item) {
            this.long_num = item.roomSn;
            this.roomId = item.id;
            this.show_num = false;
        },
        // 办理入1
        bthDeal() {
            if (!this.IDcard || !/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(this.IDcard)) {
                Toast({
                    message: '请输入正确的身份证号',
                    duration: 1000,
                    className: 'toasts'
                });
                this.IDcard = '';
                return false;
            }
            if (this.long_num == '请选择房间号') {
                Toast({
                    message: '请选择房间号',
                    duration: 1000,
                    className: 'toasts'
                });
                return false;
            }
            // 已验证191,181手机号
            if (!this.phone || !/^1[3456789]\d{9}$/.test(this.phone)) {
                Toast({
                    message: '请输入正确的手机号',
                    duration: 1000,
                    className: 'toasts'
                });
                this.phone = '';
                return false;
            }
            const _this = this;
            MessageBox.confirm(
                `姓名：${_this.userName}<br />
        证件号：${_this.IDcard}<br />
        房间号：${_this.long_num}<br />
        手机号：${_this.phone}<br />
        车牌号：${!_this.carNum ? '空' : _this.carNum}`,
                '办理入住信息确认'
            )
                .then(() => {
                    this.roomAssignComfirm();
                })
                .catch(() => {});
        },
        // 办理入住2
        roomAssignComfirm() {
            let orderRoomId = this.crrentId;
            if (!this.month) {
                let params = Object.assign({
                    roomId: this.roomId,
                    roomUser: this.userName,
                    idCard: this.IDcard,
                    phone: this.phone,
                    carNo: this.carNum,
                    remark: ''
                });
                PostBusinessOrderRoomin(orderRoomId, params).done((res) => {
                    Toast({
                        message: res.msg,
                        duration: 1000
                    });
                    setTimeout('window.history.go(-1)', 2000);
                });
            } else {
                let params = Object.assign({
                    orderId: this.orderId,
                    roomId: this.roomId,
                    roomUser: this.userName,
                    idCard: this.IDcard,
                    phone: this.phone,
                    carNo: this.carNum,
                    remark: ''
                });
                PostBusinessOrderroomRoomin(orderRoomId, params).done((res) => {
                    Toast({
                        message: res.msg,
                        duration: 1000
                    });
                    setTimeout('window.history.go(-1)', 2000);
                });
            }
        }
    }
};
</script>
<style scoped>
.long {
    display: flex;
    line-height: 35px;
    font-size: 16px;
    background: #fff;
    padding: 0 10%;
}
.popup {
    width: 100%;
}
.num_box {
    line-height: 34px;
    border-bottom: 1px solid #f5f5f5;
}
</style>



